/* Set a background image or gradient for the entire page */
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background: linear-gradient(to bottom right, #ffffff, #f1f1f1);
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.container {
  background-color: #ffffff;
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 600px;
  text-align: center;
  margin-top: -200px;
}

/* Adding smooth transitions for hover effects */
button, input[type="file"] {
  transition: all 0.3s ease;
}

button:hover {
  background-color: #d32f2f; /* Red on hover */
  color: white;
}
